<template>
  <div>
    <el-upload
      class="upload-demo"
      accept=".pdf, .doc, .docx, .xls, .xlsx"
      drag
      :action="uploadUrl"
      :headers="headerObj"
      multiple
      :show-file-list="false"
      :on-success="handleSuccess"
      :auto-upload="true"
    >
      <i class="el-icon-upload"></i>
      <div class="el-upload__text"></div>
      <div class="mt-6">
        <el-button type="primary" size="medium"></el-button>
        <el-button size="medium" @click.stop="onDownload" class="bg-[#eee]" v-if="showDownloadBtn"></el-button>
      </div>
      <div class="m-4 text-[#a9aeb7]" v-if="desc">{{ desc }}</div>
      <div class="mb-2 text-[#a9aeb7]" v-if="countContent">{{ countContent }}</div>
    </el-upload>
    <div class="flex flex-wrap mx-[-6px] pt-2">
      <div class="w-2/6 p-[6px]" v-for="(item, i) of list" :key="i">
        <el-card shadow="always" class="relative">
          <div class="flex items-center">
            <div class="h-[40px]">
              <a :href="item.url" target="_Blank" class="mr-2"><img class="h-[40px]" src="@/assets/word.png" v-if="getFileExtension(item.url) == 'docx' || getFileExtension(item.url) == 'doc'" /></a>
              <a :href="item.url" target="_Blank" class="mr-2"><img class="h-[40px]" src="@/assets/excel.png" v-if="getFileExtension(item.url) == 'xlsx' || getFileExtension(item.url) == 'xls'" /></a>
              <a :href="item.url" target="_Blank" class="mr-2"><img class="h-[40px]" src="@/assets/pdf.png" v-if="getFileExtension(item.url) == 'pdf'" /></a>
            </div>
            <div class="flex-1 overflow-hidden">
              <div class="break-all truncate">{{ item.name }}</div>
              <div class="text-xs text-[#999]">{{ item.size }}</div>
            </div>
          </div>
          <div class="absolute right-[-6px] top-[-6px]">
            <img class="w-[20px]" src="@/assets/remove.png" @click="onDelete(i)" />
          </div>
        </el-card>
      </div>
    </div>
  </div>
</template>

<script>
import { bytesToSize } from "@/utils/validate"
import { uploadFileApi } from "@/api/home"

export default {
  props: {
    showDownloadBtn: { type: Boolean, default: false },
    desc: { type: String, default: "" },
    templateUrl: { type: String, default: "" },
    countContent: { type: String, default: "" },
  },
  data() {
    return {
      uploadUrl: uploadFileApi,
      headerObj: {
        Authorization:
          "Authorization",
      },
      list: [],
    }
  },
  methods: {
    getFileExtension(filename) {
      return /[.]/.exec(filename) ? /[^.]+$/.exec(filename)[0] : ""
    },
    getFileList(response, res) {
      const list = []
      console.log("response",response.data.f)
      console.log("res",res)
      list.push({
        name: response.data.fileName,
        url: response.data.url,
        fileType: response.data.fileType,
        size: bytesToSize(response.data.fileSize),
      })
      return list
    },
    handleSuccess(response, res) {
      if (response.code == "000000") {
        this.list = this.list.concat(this.getFileList(response, res))
        this.$emit("getFile", this.list)
      } else {
        this.$message.error(response.codeDesc)
      }
    },
    onDelete(i) {
      this.list.splice(i, 1)
      this.$emit("getFile", this.list)
    },
    onDownload() {
      const url = this.templateUrl
      window.open(url)
    },
  },
}
</script>

<style lang="scss">
.el-upload {
  display: block;
}
</style>
