import request from "@/utils/request"

const api = "http://mintroy.com/medicalapi"
const host = process.env.NODE_ENV === "production" ? api : "http://localhost:8080/proxy_dev"

export function getFileApi(data) {
  return request({
    url: `${host}/medical/article/writer`,
    method: "post",
    params: data,
  })
}

export const uploadFileApi = `${host}/upload/file`
